/* Login Page Styles */
.login-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 50px 20px;
  background-color: var(--light-bg);
  min-height: calc(100vh - 300px); /* Account for header and footer */
}

.login-form-container {
  background-color: white;
  border-radius: var(--border-radius);
  box-shadow: var(--box-shadow);
  padding: 40px;
  width: 100%;
  max-width: 450px;
}

.login-form-container h2 {
  color: var(--primary-dark);
  text-align: center;
  margin-bottom: 30px;
  font-size: 2rem;
}

.login-form {
  margin-bottom: 20px;
}

.login-button {
  width: 100%;
  margin-top: 15px;
  padding: 14px;
  font-size: 1.1rem;
}

.login-footer {
  margin-top: 30px;
  text-align: center;
  color: #666;
}

.login-footer p {
  margin-bottom: 15px;
}

.login-footer a {
  color: var(--primary-color);
  text-decoration: none;
  transition: color 0.3s;
}

.login-footer a:hover {
  color: var(--primary-dark);
  text-decoration: underline;
}

.back-link {
  display: inline-block;
  margin-top: 10px;
  font-size: 0.9rem;
}

.dm-note {
  font-size: 0.9rem;
  font-style: italic;
  color: #888;
  margin-top: 5px;
}

/* Responsive */
@media (max-width: 480px) {
  .login-form-container {
    padding: 25px;
  }
}